import { post, get } from "../utils/rpc";
import { IRes } from "./students";

export interface IColorItem {
  grade: string;
  red: number;
  yellow: number;
  blue: number;
  green: number;
}

interface IFetchColorListRes extends IRes {
  list_grades_color: IColorItem[];
}

export const fetchColorList = (): Promise<IFetchColorListRes> => {
  return get({ url: "/star_reading_color" });
};

export const modifyColor = (param: IColorItem): Promise<IRes> => {
  return post({ url: "/star_reading_color", body: { ...param } });
};
