import { message } from "antd";
import { useEffect, useState } from "react";

import {
  IColorItem,
  fetchColorList,
  modifyColor,
} from "../../services/starLine";

export interface IColor extends IColorItem {
  isModify: boolean;
}

export const useColorList = (count: number) => {
  const [colorList, setColorList] = useState<IColor[]>([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchColorList()
      .then((res) => {
        const temp = res.list_grades_color.map((v) => ({
          ...v,
          isModify: false,
        }));
        setColorList(temp);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count]);

  return {
    loading,
    colorList,
    setColorList,
    setLoading,
  };
};

export const modifyColorAction = async (item: IColor) => {
  if (!item.red) {
    message.error("请输入红色比例");
    return;
  }
  if (!item.yellow) {
    message.error("请输入红色比例");
    return;
  }
  if (!item.blue) {
    message.error("请输入红色比例");
    return;
  }
  if (!item.green) {
    message.error("请输入红色比例");
    return;
  }
  const total =
    Number(item.green) +
    Number(item.red) +
    Number(item.yellow) +
    Number(item.blue);

  if (total !== 1) {
    message.error("总比例不等于1");
    return;
  }

  const res = await modifyColor(item);
  return res.executed;
};
