import React, { useMemo } from "react";
import { ColumnCenter, RowCenter } from "../../../styled";
import { HeaderWrap, HeaderItem, HeaderText, Line } from "../CCSS/styled";
import { ColorBar } from "./component/ColorBar";
import { useStarReadingData } from "./action";
import { Button, Popconfirm, Tabs } from "antd";
import { SubTitle } from "./styled";
import { DetailList } from "./component/DetailList";
import LoadingPage from "../../../../components/loading";

import { ScoreList } from "./component/ScoreList";
import { deleteStarReading } from "../../../../services/students";
import { PdfDownload } from "../../../../components/PdfView/PdfDownload";


export const demoColor = [
  {
    color: "green",
    text: "测试通过",
  },
  {
    color: "yellow",
    text: "需要加强",
  },
  {
    color: "red",
    text: "需要干预",
  },
  {
    color: "gray",
    text: "未测试",
  },
];

const { TabPane } = Tabs;

export const StarReading: React.FC<{
  type: string;
  phone: string;
  updateAction: React.Dispatch<React.SetStateAction<number>>;
}> = ({ type, phone, updateAction }) => {
  const { loading, starReadingData } = useStarReadingData(type, phone);

  const mapHeaderArr = [
    {
      label: "测试日期",
      value: starReadingData.test_date,
    },
    {
      label: "测试时长(minutes)",
      value: starReadingData.test_duration,
    },
  ];

  const scaledScore = useMemo(() => {
    const item = starReadingData.star_reading_scores.find(
      (v) => v.short_desc === "SS"
    );
    if (item) {
      return item.item_score;
    }
    return 0;
  }, [starReadingData.star_reading_scores]);

  return (
    <div>
      <Tabs onChange={(key) => {}} type="card" size="large">
        <TabPane tab="Star Reading报告详情" key="1">
          <ColumnCenter>
            <HeaderWrap style={{ marginTop: 10 }}>
              {mapHeaderArr.map((v, i) => (
                <HeaderItem
                  key={i}
                  style={i % 2 === 0 ? {} : { textAlign: "right" }}
                >
                  <span>{v.label}</span>
                  <HeaderText style={i === 1 ? { fontWeight: "bolder" } : {}}>
                    {v.value}
                  </HeaderText>
                </HeaderItem>
              ))}
            </HeaderWrap>
            <Line></Line>

            <ColorBar
              benchmark={starReadingData.district_benchmark}
              num={scaledScore}
              grade={starReadingData.grade}
            />
            <Line></Line>

            <SubTitle>Star Reading测试指标分值</SubTitle>
            <ScoreList data={starReadingData.star_reading_scores} />
            <Line></Line>
            <SubTitle>Star Reading各项技能测试得分</SubTitle>
            <ScoreList
              data={starReadingData.literature_scores}
              subTitle="Literature"
              bolder
            />
            <ScoreList
              data={starReadingData.information_text_scores}
              subTitle="Informational Text"
              bolder
            />
            <ScoreList
              data={starReadingData.language_scores}
              subTitle="Language"
              bolder
            />
            <Line></Line>
            <SubTitle style={{ marginBottom: 20 }}>
              Star Reading报告详情
            </SubTitle>
            {starReadingData.report_details.map((v, i) => (
              <DetailList key={i} {...v} />
            ))}
          </ColumnCenter>
        </TabPane>
        <TabPane tab="Star报告文字版" key="2">
          <PdfDownload url={starReadingData.main_pdf_url} />
        </TabPane>
        <TabPane tab="Star报告简版" key="3">
          <PdfDownload url={starReadingData.simple_pdf_url} />
        </TabPane>
      </Tabs>
      <Popconfirm
        title="确定删除当前报告？"
        placement="topLeft"
        transitionName=""
        onConfirm={async () => {
          const res = await deleteStarReading(phone, type);
          if (res.executed) {
            updateAction((pre) => pre + 1);
          }
        }}
      >
        <Button
          size="large"
          type="primary"
          style={{ position: "absolute", right: "30px", top: 0 }}
        >
          删除报告
        </Button>
      </Popconfirm>
      <LoadingPage isLoading={loading} />
    </div>
  );
};
