import React, { useRef, useState } from "react";
import { Wrap } from "./styled";
import {
  Modal,
  Table,
  Input,
  Popconfirm,
  Button as AntButton,
  message,
} from "antd";
import { Button } from "../../components/Button";
import "./index.scss";
import { Course } from "./course";
import {
  handleSearchResult,
  modifyStudentAction,
  useCourseList,
  useServiceList,
  useStudents,
} from "./action";
import LoadingPage from "../../components/loading";
import {
  deleteStudent,
  fetchStudents,
  IStudent,
} from "../../services/students";
import { CustomSelect } from "../../components/CustomSelect";
import { InputList } from "../../components/InputList";
import { RowCenter } from "../styled";

const webAccelerator = [
  {
    key: "华为-上海-LA-新",
    value: "华为-上海-LA-新",
  },
  {
    key: "阿里03-日本东京",
    value: "阿里03-日本东京",
  },
  {
    key: "阿里02-洛杉矶",
    value: "阿里02-洛杉矶",
  },
  {
    key: "阿里02-俄勒冈",
    value: "阿里02-俄勒冈",
  },
  {
    key: "阿里01-佛罗里达",
    value: "阿里01-佛罗里达",
  },
];

export default () => {
  const [count, setCount] = useState(0);
  const [current, setCurrent] = useState(1);
  const [studentId, setStudentId] = useState(0);
  const [phone, setPhone] = useState("");
  const [pageSize, setPageSize] = useState(10);

  const searchInfo = useRef({ value: "" }).current;
  const { courseList } = useCourseList();
  const { serviceList } = useServiceList();

  const { loading, data, setData, setLoading, totalCount } = useStudents(
    count,
    current
  );

  const [visible, setVisible] = useState(false);

  const handleChangeValue = (
    key: keyof IStudent,
    value: any,
    item: IStudent
  ) => {
    const newData = data.map((v) => {
      if (v.id === item.id) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setData(newData);
  };

  const columns = [
    {
      title: "用户ID",
      dataIndex: "id",
      key: "id",
    },
    {
      title: "用户名",
      dataIndex: "username",
      key: "username",
      render: (value: string, item: IStudent) => {
        if (item.isModify) {
          return (
            <Input
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("username", e.target.value, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "密码",
      dataIndex: "password",
      key: "password",
      render: (value: string, item: IStudent) => {
        if (item.isModify) {
          return (
            <Input
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("password", e.target.value, item);
              }}
            />
          );
        } else {
          return value ? "***" : "";
        }
      },
    },
    {
      title: "手机号",
      dataIndex: "phone_number",
      key: "phone_number",
      render: (value: string, item: IStudent) => {
        if (item.isModify && item.id === 0) {
          return (
            <Input
              type="number"
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("phone_number", e.target.value, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "加速器端口",
      dataIndex: "web_accelerator_name",
      key: "web_accelerator_name",
      render: (value: string, item: IStudent) => {
        if (item.isModify) {
          return (
            <CustomSelect
              initValue={value}
              selectData={webAccelerator}
              onChange={(key) => {
                handleChangeValue("web_accelerator_name", key, item);
              }}
            />
          );
        } else {
          return webAccelerator.find((v) => v.key === value)?.value;
        }
      },
    },
    {
      title: "加速器链接",
      dataIndex: "web_accelerator_link",
      key: "web_accelerator_link",
      render: (value: string, item: IStudent) => {
        if (item.isModify) {
          return (
            <Input
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("web_accelerator_link", e.target.value, item);
              }}
            />
          );
        } else {
          return value;
        }
      },
    },
    {
      title: "购买课程数量",
      dataIndex: "courses_count",
      key: "courses_count",
    },
    {
      title: "操作",
      key: "operate",
      render: (item: IStudent) => {
        if (item.isModify) {
          return (
            <>
              <Button
                text="确定"
                style={{ margin: "0 5px" }}
                onPress={async () => {
                  console.log("item", item);
                  const res = await modifyStudentAction(item);
                  if (res) {
                    if (item.id === 0) {
                      setPageSize(10);
                      if (current===1) {
                        setCount((v) => v + 1);
                      } else {
                        setCurrent(1);
                      }
                    } else {
                      setCount((v) => v + 1);
                    }
                  }
                }}
              />
              <Button
                style={{ margin: "0 5px" }}
                text="取消"
                onPress={async () => {
                  if (item.id === 0) {
                    setPageSize(10);
                  }
                  setCount((v) => v + 1);
                }}
              />
            </>
          );
        }
        return (
          <>
            <Button
              text="修改"
              style={{ margin: "0 5px" }}
              onPress={async () => {
                const newData = data.map((v) =>
                  v.id === item.id ? { ...v, isModify: true } : v
                );
                setData(newData);
              }}
            />
            <Popconfirm
              style={{ margin: "0 5px" }}
              title="确定删除此学生？"
              placement="topLeft"
              transitionName=""
              onConfirm={async () => {
                setLoading(true);
                const res = await deleteStudent(item.id);
                setLoading(false);
                if (res.executed) {
                  setCount((v) => v + 1);
                }
              }}
            >
              <AntButton>删除</AntButton>
            </Popconfirm>
          </>
        );
      },
    },
    {
      title: "课程详情",
      key: "name5",
      render: (item: IStudent) => (
        <Button
          text="查看"
          disabled={item.isModify}
          onPress={() => getDetail(item.id, item.phone_number)}
        />
      ),
    },
  ];

  const searchArr = [
    {
      label: "用户信息",
      initValue: searchInfo.value,
      placeholder: "请输入用户名或手机号",
      onChange: (value: string) => {
        searchInfo.value = value;
      },
      inputStyle: {
        width: "300px",
      },
      onKeyUp: async (e: any) => {
        if (e.keyCode === 13) {
          await handleSearch();
        }
      },
    },
  ];

  const getDetail = async (id: number, phone: string) => {
    setStudentId(id);
    setPhone(phone);
    setVisible(true);
  };

  const handleSearch = async () => {
    if (!searchInfo.value) {
      message.warn("请输入用户信息");
      return;
    }
    const data = await handleSearchResult(searchInfo.value);
    setData(data);
    // setCurrent(1);
  };

  return (
    <Wrap style={{ paddingTop: 10 }}>
      <RowCenter style={{ justifyContent: "space-between" }}>
        <RowCenter>
          <InputList
            inputData={searchArr}
            style={{
              marginRight: 30,
            }}
          />
          <Button
            text="搜索"
            onPress={handleSearch}
            style={{ marginRight: 30 }}
          />
          <Button
            text="查看全部"
            onPress={async () => {
              setCurrent(1);
              setCount((pre) => pre + 1);
            }}
          />
        </RowCenter>
        <Button
          onPress={async () => {
            if (data.filter((v) => v.id === 0).length > 0) {
              message.error("请先完成当前新增");
              return;
            }

            setPageSize(11);
            setData((pre) => [
              {
                id: 0,
                username: "",
                phone_number: "",
                web_accelerator_name: "",
                web_accelerator_link: "",
                password: "",
                isModify: true,
                courses_count: 0,
              },
              ...pre,
            ]);
          }}
          text="新增用户"
        />
      </RowCenter>
      <Table
        pagination={{
          showSizeChanger:false,
          hideOnSinglePage: true,
          total: totalCount,
          current: current,
          onChange: (page) => {
            setCurrent(page);
          },
          pageSize,
          pageSizeOptions: [],
        }}
        rowKey={(item) => item.id}
        dataSource={data}
        columns={columns.map((v) => ({ ...v, align: "center" }))}
      />
      <Modal
        style={{ width: 120 }}
        title=""
        visible={visible}
        onCancel={() => setVisible(false)}
        onOk={() => setVisible(false)}
        wrapClassName={"detail-modal"}
        footer={null}
      >
        <Course
          id={studentId}
          courseList={courseList}
          serviceList={serviceList}
          phone={phone}
        />
      </Modal>
      <LoadingPage isLoading={loading} />
    </Wrap>
  );
};
