import { message } from "antd";
import { useEffect, useState } from "react";
import {
  fetchStudents,
  IStudent,
  modifyStudent,
  fetchCourseDetail,
  getCoursesList,
  modifyCourseDetail,
  modifyCourseDetailParams,
  IListItem,
  getServicesList,
  searchStudent,
} from "../../services/students";
import { validatePhone } from "../../utils/validate";

export interface ICourseDetail {
  course_id: string;
  course_school_code: string;
  course_user_name: string;
  course_user_password: string;
  created: any;
  customer_service: string;
  ended_date: any;
  id: number;
  isModify: boolean;
}

export const useStudents = (count: number,currentPage:number) => {
  const [data, setData] = useState<IStudent[]>([]);
  const [totalCount, setTotalCount] = useState(0);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchStudents(currentPage)
      .then((v) => {
        const newData = v?.data_list?.map((v) => ({
          ...v.user,
          phone_number: v.phone_number,
          web_accelerator_name: v.web_accelerator_name,
          web_accelerator_link: v.web_accelerator_link,
          isModify: false,
          courses_count: v.courses_count,
        }));
        setTotalCount(v.count);
        // newData.reverse();
        setData(newData);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count,currentPage]);

  return {
    loading,
    data,
    setData,
    setLoading,
    totalCount
  };
};

export const modifyStudentAction = async (params: IStudent) => {
  console.log(params);
  if (!params.username) {
    message.error("请输入用户名");
    return;
  }
  const reg = /^[-_0-9a-zA-Z\u4e00-\u9fa5]{1,}$/;
  if (!reg.test(params.username)) {
    message.error(
      "用户名只能由中英文、数字、-、和_组成，不能包含空格与其他特殊字符"
    );
    return;
  }
  if (!params.password) {
    message.error("请输入密码");
    return;
  }
  const phoneTemp = validatePhone(params.phone_number);
  if (!phoneTemp.state) {
    message.error(phoneTemp.msg);
    return;
  }
  const res = await modifyStudent(params);
  return res.executed;
};

export const useCourseDetail = (count: number, id: number) => {
  const [data, setData] = useState<ICourseDetail[]>([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    setLoading(true);
    fetchCourseDetail(id)
      .then((v) => {
        const newData = v?.data_list.map((v) => ({
          ...v,
          created: v.created.split("T")[0],
          ended_date: v.ended_date.split("T")[0],
        }));
        newData.reverse();
        setData(newData);
      })
      .finally(() => {
        setLoading(false);
      });
  }, [count, id]);

  return {
    loading,
    data,
    setData,
    setLoading,
  };
};

