export const modifyCourseDetailAction = async (
  params: modifyCourseDetailParams
) => {
  console.log(params);
  if (!params.course_id) {
    message.error("请选择课程名称");
    return;
  }
  if (!params.course_user_name) {
    message.error("请输入账号");
    return;
  }
  if (!params.course_user_name) {
    message.error("请输入密码");
    return;
  }
  const res = await modifyCourseDetail(params);
  return res.executed;
};

export const useCourseList = () => {
  const [courseList, setCourseList] = useState<IListItem[]>([]);
  useEffect(() => {
    getCoursesList().then((res) => {
      const temp = res?.data_list.map((v) => ({
        key: v.id,
        value: v.display_name,
      }));
      setCourseList(temp);
    });
  }, []);

  return {
    courseList,
  };
};

export const useServiceList = () => {
  const [serviceList, setServiceList] = useState<IListItem[]>([]);
  useEffect(() => {
    getServicesList().then((res) => {
      const temp = res?.data_list.map((v) => ({
        key: v.id,
        value: v.customer_service_name,
      }));
      setServiceList(temp);
    });
  }, []);

  return {
    serviceList,
  };
};

export const handleSearchResult = async (value: string) => {
  const res = await searchStudent(value);
  const newData = res?.data_list?.map((v) => ({
    ...v.user,
    phone_number: v.phone_number,
    web_accelerator_name: v.web_accelerator_name,
    web_accelerator_link: v.web_accelerator_link,
    isModify: false,
    courses_count: v.courses_count,
  }));
  newData.reverse();
  return newData;
};
