import { useEffect, useState } from "react";
import { fetchTestList } from "../../../services/students";


interface IList {
  ccss_tests: string[];
  star_early_tests: string[];
  star_reading_tests: string[];
}

export const useListData = (phone_number: string, updateCode:number) => {
  const [loading, setLoading] = useState(true);
  const [list, setList] = useState<IList>({
    ccss_tests: [],
    star_early_tests: [],
    star_reading_tests: [],
  });

  useEffect(() => {
    fetchTestList(phone_number)
      .then((res) => {
        setList({
          ccss_tests: res.ccss_tests,
          star_early_tests: res.star_early_tests,
          star_reading_tests: res.star_reading_tests,
        });
      })
      .finally(() => {
        setLoading(false);
      });
  }, [phone_number,updateCode]);

  return {
    loading,
    list,
  };
};
