import React, { useState } from "react";
import { Title, Wrap } from "./styled";
import { Table, Input, message } from "antd";
import { Button } from "../../components/Button";
import LoadingPage from "../../components/loading";
import { useColorList, modifyColorAction, IColor } from "./action";

export default () => {
  const [count, setCount] = useState(0);
  const { colorList, loading, setColorList } = useColorList(count);

  const handleChangeValue = (key: keyof IColor, value: any, item: IColor) => {
    const newData = colorList.map((v) => {
      if (v.grade === item.grade) {
        return { ...v, [key]: value };
      }
      return v;
    });
    setColorList(newData);
  };

  const columns = [
    {
      title: "年级",
      dataIndex: "grade",
      key: "grade",
    },
    {
      title: "红",
      dataIndex: "red",
      key: "red",
      render: (value: string, item: IColor) => {
        if (item.isModify) {
          return (
            <Input
              type="number"
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("red", e.target.value, item);
              }}
            />
          );
        } else {
          return value ? value : "-";
        }
      },
    },
    {
      title: "黄",
      dataIndex: "yellow",
      key: "yellow",
      render: (value: string, item: IColor) => {
        if (item.isModify) {
          return (
            <Input
              type="number"
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("yellow", e.target.value, item);
              }}
            />
          );
        } else {
          return value ? value : "-";
        }
      },
    },
    {
      title: "蓝",
      dataIndex: "blue",
      key: "blue",
      render: (value: string, item: IColor) => {
        if (item.isModify) {
          return (
            <Input
              type="number"
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("blue", e.target.value, item);
              }}
            />
          );
        } else {
          return value ? value : "-";
        }
      },
    },
    {
      title: "绿",
      dataIndex: "green",
      key: "green",
      render: (value: string, item: IColor) => {
        if (item.isModify) {
          return (
            <Input
              type="number"
              allowClear
              className="auto-table-input"
              placeholder="请填写"
              value={value}
              onChange={(e) => {
                handleChangeValue("green", e.target.value, item);
              }}
            />
          );
        } else {
          return value ? value : "-";
        }
      },
    },
    {
      title: "操作",
      key: "operate",
      render: (item: IColor) => {
        if (item.isModify) {
          return (
            <>
              <Button
                text="确定"
                style={{ margin: "0 5px" }}
                onPress={async () => {
                  const res = await modifyColorAction(item);
                  if (res) {
                    setCount((v) => v + 1);
                  }
                }}
              />
              <Button
                style={{ margin: "0 5px" }}
                text="取消"
                onPress={async () => {
                  setCount((v) => v + 1);
                }}
              />
            </>
          );
        }
        return (
          <Button
            text="修改"
            style={{ margin: "0 5px" }}
            onPress={async () => {
              const flag = colorList.some((v) => v.isModify);
              if (flag) {
                message.error("请先保存当前修改");
                return;
              }
              const newData = colorList.map((v) =>
                v.grade === item.grade ? { ...v, isModify: true } : v
              );
              setColorList(newData);
            }}
          />
        );
      },
    },
  ];

  return (
    <Wrap>
      <Title style={{ textAlign: "center" }}>Star基线图形尺寸比例设置</Title>
      <Table
        pagination={{
          hideOnSinglePage: true,
          pageSize: 12,
        }}
        rowKey={(item) => item.grade}
        dataSource={colorList}
        columns={columns.map((v) => ({ ...v, align: "center" }))}
      />
      <LoadingPage isLoading={loading} />
    </Wrap>
  );
};
