import React, { FC, useEffect, useMemo, useState } from "react";
import { Menu } from "antd";
import { Content, RightWrap } from "./styled";
import { StarEarly } from "./starEarly";
import { Map } from "./CCSS";
import { StarReading } from "./StarReading";
import { useListData } from "./action";
import LoadingPage from "../../../components/loading";
const { SubMenu } = Menu;

const rootSubMenuKeys = ["sub1", "sub2", "sub3"];

export const Evaluate: FC<{ phone: string }> = ({ phone }) => {
  const [openKeys, setOpenKeys] = React.useState(["sub1"]);
  const [selectedKeys, setSelectedKeys] = React.useState<string[]>([]);
  const [currentType, setCurrentType] = React.useState<string>("");
  const [updateCode, setUpdateCode] = useState(0);

  const { loading, list } = useListData(phone, updateCode);

  useEffect(() => {
    console.log("list", list);
  }, [list]);

  const menuData = useMemo(() => {
    return [
      {
        title: "CCSS Test",
        key: "sub1",
        data: list.ccss_tests,
      },
      {
        title: "Star Reading Test",
        key: "sub2",
        data: list.star_reading_tests,
      },
      {
        title: "Star Early Test",
        key: "sub3",
        data: list.star_early_tests,
      },
    ];
  }, [list]);

  useEffect(() => {
    console.log("phone", phone);
    if (!loading) {
      console.log("loading", loading);
      for (let v of menuData) {
        console.log("menuData", v);
        if (v.data?.length > 0) {
          console.log("phone11", phone);
          setSelectedKeys([`${v.data[0]}_${v.title}`]);
          setOpenKeys([v.key]);
          setCurrentType(v.title);
          return;
        }
      }
      setSelectedKeys(["sub1"]);
      setOpenKeys([]);
      setCurrentType("");
    }
  }, [loading, list]);

  const onOpenChange = (keys: any) => {
    console.log(keys);
    const latestOpenKey = keys.find((key: any) => openKeys.indexOf(key) === -1);
    if (rootSubMenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };

  const handleClick = (e: any) => {
    setCurrentType(e.key.split("_")[1]);
    setSelectedKeys([e.key]);
  };

  const rightContent = useMemo(() => {
    if (currentType === "CCSS Test") {
      return (
        <Map
          type={selectedKeys[0].split("_")[0]}
          phone={phone}
          updateAction={setUpdateCode}
        />
      );
    }
    if (currentType === "Star Early Test") {
      return (
        <StarEarly
          type={selectedKeys[0].split("_")[0]}
          phone={phone}
          updateAction={setUpdateCode}
        />
      );
    }
    if (currentType === "Star Reading Test") {
      return (
        <StarReading
          type={selectedKeys[0].split("_")[0]}
          phone={phone}
          updateAction={setUpdateCode}
        />
      );
    }
    return <div>暂无数据</div>;
  }, [currentType, selectedKeys]);

  return (
    <div style={{ width: "100%", padding: 20, paddingBottom: 10 }}>
      <Content>
        <Menu
          selectedKeys={selectedKeys}
          mode="inline"
          openKeys={openKeys}
          style={{ width: 250, fontSize: 18 }}
          onOpenChange={onOpenChange}
          onClick={handleClick}
        >
          {menuData.map((v) => (
            <SubMenu key={v.key} title={v.title}>
              {v.data?.map((item) => (
                <Menu.Item key={`${item}_${v.title}`}>{item}</Menu.Item>
              ))}
            </SubMenu>
          ))}
        </Menu>
        <RightWrap style={{ position: "relative" }}>{rightContent}</RightWrap>
      </Content>
      <LoadingPage isLoading={loading} />
    </div>
  );
};
