import React, { useEffect, useState } from "react";
import Students from "./Students";
import Courses from "./Course";
import Services from "./Services";
import Test from "./Test";
import Report from "./UploadReport";
import Point from "./PointInput";
import Product from "./Product";
import StarLine from "./StarLine";

import { useHistory, Switch, Route } from "react-router";
import { getCookieObj, useScrollTop } from "../utils/common";
import { setItem, StoreEnum } from "../utils/store";
import LoadingPage from "../components/loading";
import { BackBtn } from "../components/Button";

const routerArr = [
  { path: `students`, component: Students },
  { path: `courses`, component: Courses },
  { path: `services`, component: Services },
  { path: `test`, component: Test },
  { path: `report`, component: Report },
  { path: `point`, component: Point },
  { path: `product`, component: Product },
  { path: `star-line`, component: StarLine },
];

export default () => {
  const history = useHistory();
  useScrollTop();

  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const data = getCookieObj();
    setItem(StoreEnum.SESSION, data.csrftoken || "");
    setLoading(false);
  }, []);

  return (
    <div style={{ width: "100%" }}>
      <BackBtn />
      <Switch>
        {loading ? (
          <LoadingPage isLoading={loading} />
        ) : (
          routerArr.map((v, i) => (
            <Route key={i} path={`/${v.path}`} component={v.component} />
          ))
        )}
      </Switch>
    </div>
  );
};
