import React from "react";
import { PdfView } from ".";
import { SubTitle } from "../../pages/Students/styled";
import { ColumnCenter } from "../../pages/styled";



export const PdfDownload = ({ url }: { url: string }) => {
  const name = url?.split("/");
  return (
    <ColumnCenter>
      {url ? (
        <div>
          <div
            style={{
              width: 1100,
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            <a
              href={url}
              style={{
                marginTop: 0,
                color: "#333",
                marginBottom: 0,
                textAlign: "right",
                fontSize: 16,
              }}
              download={name[name.length - 1]}
            >
              点击下载PDF
            </a>
          </div>
          <PdfView pdfUrl={url} />
        </div>
      ) : (
        <SubTitle
          style={{
            marginTop: 0,
            color: "#333",
            marginBottom: 0,
            textAlign: "left",
          }}
        >
          暂无数据
        </SubTitle>
      )}
    </ColumnCenter>
  );
};

